@import './variables';
@import './mixins.scss';

*{
  box-sizing:border-box;
  margin:0;
  padding:0;
}

body {
  margin: 0;
  font-family: $font-stack;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
}

.App{
  background: $secondary-color;
  color: $primary-font-color;
  margin: 0px auto;
  position: relative;
  
  .header{
    display: flex;
    justify-content:space-between;
    align-items:center;
    background: $primary-color;
    position: sticky;
    top: 0;
    margin-bottom: 20px;
    padding: 10px 20px;
    border-bottom:2px solid $gold;  
    
    p{
      margin:0 10px;
    }
    
    h1{
      margin: 10px 0;

      @include for-size(LGphone){
        font-size: 2rem;
      }

    }
    
    .title{
      display: flex;
      align-items: center;
      
      img{
        margin-right: 10px;
        @include for-size(tablet){
          display: none;
        }

      }
    }
    
    .user-logout{
      display: flex;
      align-items: center;

      @include for-size(LGphone){
        display: grid;
      }
      
      p{
        color:$primary-font-color;
        margin-right: 10px;
        font-size: 1.3rem;
        text-align: center;

        @include for-size(LGphone){
          font-size: 1.1rem;
          text-align: center;
        }

      }
    }
    
  }
  
  
  .main{
    min-height: calc(100vh - 110px - 185px);
    
    .button{
      text-align: center;
      padding: 20px;
    }
    
    
    .recipe-list{
      width: 50%;
      max-width: 800px;
      margin: 20px auto;
      
      
      @include for-size(tablet){
        width: 70%;

      }
      @include for-size(LGphone){
        width:100%;
        padding:0 10px;
        
      }
      
      
      
      .title{
        color: $secondary-font-color;
        text-align: center;
        font-weight:600;
        font-size: 1.8rem;
        padding-bottom: 10px;

        @include for-size(LGphone){
          font-size: 1.5rem;
        }

      }
      
      .ingredients{
        color: $primary-font-color;
        font-size: 1em;
        line-height: 2;
        padding: 10px;
        white-space: pre-line;
        border-top: 1px solid $gold;
        border-bottom: 1px solid $gold;


      }
      
      h2{
        color: $gold;
        margin-top: 30px;
      }
      
      ul{
        padding: 0;
        list-style-type: none;
        
        li{
          background: $primary-color;
          padding: 10px;
          margin: 0 0 30px;
          border-radius: 5px;
          
          
          .show-hide-ingredients{
            display: flex;
            align-items: center;
            justify-content: center;
            color: $form-color;
            font-size: 1.1rem;
            cursor: pointer;
          }
                    
          .fav-del-edit{
            display: flex;
            justify-content:space-around;
            align-items: center;
            margin: 20px 10px 10px 10px;
          }
          
        }
      }
    }

    .empty{
      text-align: center;
      margin: 20px;
      font-size: 2rem;
    }

    .content{
      text-align: center;
      
    
  }
    
  }
  
  footer{
    background: $secondary-footer-color;
    color: $secondary-font-color; 
    width: 100%;
    margin-top: 20px;
    border-top: 2px solid $gold;
    
    .footer-content{
      display: flex;
      justify-content: center;
      align-items: center;
      text-align: center;
      
      .socials{
        display: flex;
        justify-content: center;
        align-items: center;
        list-style: none;
        margin-bottom: 10px;
        padding:0;
        
        li{
          margin: 0 10px;
        }
        
        a{
          text-decoration: none;
          color: $secondary-font-color;
          font-size: 1.8rem;
        
          &:hover{
            color:$gold;
            transition: 0.3s ease-out;
          }
        }
        
        
      }
    }
    
    .footer-bottom{
      background: $primary-footer-color;
      width:100%;
      padding:20px 0;
      text-align: center;
      
      p{
        font-size: 14px;
        word-spacing: 2px;
        text-transform: capitalize;
        font-size: 0.8rem;
      }
    }
    
  }
  
  
  
}

.form-login{
  width: 60%;
  max-width: 300px;

}

form{
  margin: 20px auto;
  width: 100%;
  max-width: 700px;
  
  
  input[type='text']{
    width: 100%;
    color: $secondary-font-color;
    background: $form-color;
    box-sizing: border-box;
    padding: 10px;
    margin: 6px 0;
    border: 0;
  }
  
  textarea {
    width: 100%;
    height: 150px;
    color: $secondary-font-color;
    background: $form-color;
    box-sizing: border-box;
    padding: 10px;
    margin: 6px 0;
    border: 0;
  }
  
  
  
}


.modal{
  
  .modal-header, .modal-footer{
    background-color:$primary-color;
  }
  
  .modal-body{
    background-color: $secondary-color;
  }
}


.modal-delete{
  
  .modal-header{
    background-color: #eee;
    color: black;
  }

  .modal-body{
    background-color: #eee;
    color: black;
    font-weight: 500;
  }
  
  .modal-footer{
  background-color: #eee;
    color: black;
  }
  
}


.fas {
  cursor: pointer;
  
}

.far{
  cursor: pointer;
}

.fa-minus-circle{  
  color: rgb(173, 22, 22);
  margin-right: 5px;
}







  
  